export default function (Component) {
  Component.__i18n = Component.__i18n || []
  Component.__i18n.push({
    "locale": "",
    "resource": {
      "en": {
        "welcome": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["welcome"])},
        "welcomeLine": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Please choose your preferred login method"])},
        "ssn": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Social Security Number / SMS TAN"])},
        "id_austria": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["ID Austria"])}
      },
      "gr": {
        "welcome": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Willkommen"])},
        "welcomeLine": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Bitte wählen Sie Ihre bevorzugte Anmeldemethode"])},
        "ssn": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["Sozialversicherungsnummer / SMS-TAN"])},
        "id_austria": (ctx) => {const { normalize: _normalize } = ctx;return _normalize(["ID österreich"])}
      }
    }
  })
}
